.flag-header {
  position: fixed;
  width: 100%;
  height: 60px;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
  background-clip: content-box;
  background-color: #fff;
  z-index: 100;
}
.flag-header .flag-header-main {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flag-header .flag-header-main .flag-header-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.flag-header .flag-header-main .flag-header-left .flag-logo {
  font-size: 31px;
  font-weight: 200;
  margin-bottom: 5px;
}
.flag-header .flag-header-main .flag-header-left nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: 20px;
}
.flag-header .flag-header-main .flag-header-left nav .nav-item {
  padding: 10px;
  font-size: 17px;
  font-weight: 200;
  color: #8590a6;
}
.flag-header .flag-header-main .flag-header-left nav .nav-item:hover {
  color: #1890ff;
}
.flag-header .flag-header-main .flag-header-right {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flag-header .flag-header-main .flag-header-right .login-btn {
  margin-right: 20px;
}
.flag-header .flag-header-main .flag-header-right .avatar-content {
  display: flex;
  justify-items: center;
  align-items: center;
  padding: 5px 20px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.flag-header .flag-header-main .flag-header-right .avatar-content:hover {
  background-color: rgba(24, 144, 255, 0.4);
  border-radius: 9px;
}
.flag-header .flag-header-main .flag-header-right .avatar-content .nickname {
  margin-left: 20px;
  font-family: 'Pacifico';
  font-size: 19px;
}
.nav-active {
  color: #1890ff;
}
.userInfo-popover {
  padding: 0 10px;
}
.userInfo-popover > div {
  padding: 10px 0;
}
.userInfo-popover .exit {
  width: 100%;
  border-top: 1px solid #ebedf0;
  font-size: 16px;
  text-align: center;
  cursor: pointer;
}
.userInfo-popover .exit span {
  margin-left: 10px;
}
.userInfo-popover .personal-center {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}
.userInfo-popover .personal-center a {
  color: inherit;
  margin-left: 10px;
}
